import React, {useState} from 'react';
import image from '../../images/logo.png';
import video from '../../videos/video.mkv';

import {Button} from '../ButtonElement';
import {
	HeroContainer,
	HeroBg,
	VideoBg,
	ImgBg,
	HeroContent,
	HeroH1,
	HeroP,
	HeroBtnWrapper,
	ArrowForward,
	ArrowRight
} from './HeroElements';
import InfoSection2	 from '../InfoSection/index2';
import {homeObjOne, homeObjTwo, homeObjThree, homeObjFour, homeObjFive} from './Data';

const HeroSection = () => {
	const [hover, setHover] = useState(false);

	const onHover = () => {
		setHover(!hover);
	}

	return(
		<>
		<HeroContainer id='home'>
			<HeroBg>
				<VideoBg autoPlay loop muted src={video}
				type='video/mp4'/>
				<ImgBg src= {image}/>
			</HeroBg>
		</HeroContainer>
		<HeroContainer id='acceuil'>
			<HeroContent>
				<HeroH1 >Centre Stratgique pour la Promotion du Dveloppement Durable</HeroH1>
				<HeroP>
					Le Centre Stratgique pour la Promotion Du Dveloppement Durable (CSPDD) a t  cr officiellement par la publication au Journal Officiel n 63 de la Rpublique Tunisienne en date du 27.05.2017.
Notre association rpond  un souci collectif de ses fondateurs de l'importance  du rle du dveloppement durable dans la prservation de nos richesses, la protection de l'environnement, la promotion des 17 objectifs du dveloppement durable et fait suite au grand vnement qui a eu lieu  l'ONU par l'adoption de 93 pays, en Septembre 2015, du programme de dveloppement durable  l'horizon 2030, comprenant les 17 objectifs du dveloppement durable  (17 ODD).

				</HeroP>
			</HeroContent>
		</HeroContainer>
		<InfoSection2 {...homeObjOne}/>
		<InfoSection2 {...homeObjTwo}/>
		<InfoSection2 {...homeObjThree}/>
		<InfoSection2 {...homeObjFour}/>
		<InfoSection2 {...homeObjFive}/>
		</>
		)
}

export default HeroSection;