import React, {useState} from 'react';
import { Sidebar } from '../../Sidebar';
import Navbar from '../../Navbar';
import Footer from '../../Footer';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import { NavLinksRoute } from '../../Navbar/NavbarElements';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Subtitle1, Heading, TextWrapper,InfoContainer,
	InfoWrapper,
	InfoRow,
	Column1,
	Column2,
	TopLine,
	BtnWrap,
	ImgWrap,
	 } from '../../InfoSection/InfoElements';
import 'react-lazy-load-image-component/src/effects/blur.css';
import CloseIcon from '@mui/icons-material/Close';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Radium, { StyleRoot } from 'radium';

const mystyle = {
    '@media screen and (max-width: 576px)': {
        alignSelf: "flex-start",
      },
}
  
const cards = [
    {   title: "Prsentation",
        subtitle: "Dans le cadre du projet  Voler de ses propres ailes , nous avons organis notre 1re runion avec 3 des associations slectionnes pour communiquer de plus prs et leur prsenter notre programme de formation et de soutien ainsi que les procdures de participation. Le prsident du Centre Stratgique pour la Promotion du Dveloppement Durable et le chef de projet ont galement chang avec les jeunes participants des difficults et/ ou des problmes de leurs associations. La runion a eu lieu le 09 octobre 2022 de 9h30  11h30 dans une ambiance dchange et de bonne humeur dans les locaux de la municipalit dEl Mourouj que nous remercions normment. 12 membres des bureaux excutifs des associations ont particip  la runion et ont exprim leur grande motivation et leur dsir de travailler dans un environnement sain de partage de connaissances et dexpriences en vue de monter de grands projets communs pour la rgion de Ben Arous ; leur devise tant  Ensemble, on est plus forts !  Nous avons galement eu une runion  distance avec 4 membres de lassociation des Jeunes Cratifs et Conscients de Mhamdia qui nont pas pu assister  la runion en prsentiel. La runion a eu lieu le 10 octobre  17h et a t trs intressante grce  lenthousiasme et  la pertinence des interventions de ces jeunes membres.Dans lattende du dmarrage de ce projet, nous sommes tous trs confiants quant  la russite de notre collaboration."}
  ];

let data = [];
for (let i = 1; i < 10; i++) {
    let photo = {};
    photo["imgSrc"] = `https://github.com/khalil-ryu/cspdd-images/blob/master/project-two/${i}.jpg?raw=true`
    data.unshift(photo);
}
const ActivitesDetails = () => {
	const[isOpen, setIsOpen] = useState(false);

	const toggle = () => {
		setIsOpen(!isOpen);
	};
    const [model,setModel]=useState(false)
    const [tempimgSrc,setTempimgSrc]=useState('')
    const getImg =(imgSrc)=>{
        setTempimgSrc(imgSrc);
        setModel(true);
    }
	return(	
		< >
		
		<Sidebar isOpen= {isOpen} toggle= {toggle}/>
		<Navbar toggle= {toggle}/>
        
        <InfoContainer lightBg={true} id={0} >
            
            <InfoWrapper >            
        			<InfoRow imgStart={true}>
						<Column1 >
							<TextWrapper>
                            <Heading lightText={false}>
                                {cards[0].heading}
                            </Heading>
                            <Subtitle1 darkText={true}>
                                {cards[0].subtitle}
                                <br>
                                </br>
                                <NavLinksRoute 
                        smooth= {true} 
                        duration= {500} 
                        spy={true}
                        exact='true'
                        to="/Activits2">
                         <ArrowBackIcon   fontSize="large" /> <p>Return</p>


                   </NavLinksRoute>
                            </Subtitle1>		
                            
							</TextWrapper>
						</Column1>
						<Column2 >
                            <ImgWrap >
                            Cliquer pour agrendir
                            <div className={model ? "model open" : "model"}>
                                <img src={tempimgSrc} />
                                <CloseIcon onClick={()=> setModel(false)}/>
                            </div>
                            <div className='gallery'>
                            {/* {loading ? (
                            <div className='loading-beta'>
                            <BeatLoader color="#36d7b7" size="80" />

                            </div>
                        ):(
                            */}
                                {
                                data.map((item,index)=> {
                                    return(
                                        <div className='pics' key={index} onClick={()=> getImg(item.imgSrc )}>
                                            <LazyLoadImage src={item.imgSrc} style={{width:'100%'}} effect="blur"/>
                                        </div>
                                    )
                                })
                                }
                        
                            </div>
     
							</ImgWrap>
						</Column2>
					</InfoRow>
				</InfoWrapper>
                
			</InfoContainer>
		<Footer/>
		</>
		)
}

export default ActivitesDetails;